import { Navigate, useLocation, useParams } from "react-router-dom"
import { types, words } from "../data/words"
import { MenuItem, MenuList, ListItemText } from "@mui/material"
import { Helmet } from "react-helmet"
import { WordGrid } from "../Component/WordGrid/WordGrid"
import { WordCard } from "../Component/WordCard/WordCard"

export const TypesCatalog = () => {
    let location = useLocation()
    let {
        word_type
    } = useParams()

    let currentType = types.find(type=>type.type == word_type)

    if(!currentType){
        return <Navigate to = '/catalog' replace />
    }

    const typesWords = words.filter(word=>word.type == word_type)

    return(
        <div>
            <Helmet>
                <title>{currentType.label}</title>
            </Helmet>
            <WordGrid>
                {typesWords.map((word, index)=><WordCard key={`${word.word}_${index}`} word={word} />)}
            </WordGrid>
        </div>
    )
}