import './WordCard.css'
import { Card, CardContent, Typography } from "@mui/material"
import { useStorage } from '../../hook/useStorage'
import { normalWord } from '../../helpers/NormalizeWord'

const types = {
    noun:'Существительное',
    adj:'Прилагательное',
    verb:'Глагол',
    partnverbadj:'Причастие/ отглагольное прилагательное',
    details:'Деепричастие',
    adverb:'Наречие'
}

export const WordCard = ({word}) => {
    let {
        isDesktop,
        isTablet,
        isMobile
    } = useStorage()

    const genClassName = () => {
        if(isDesktop){return 'wordcard desktop_wordcard'}
        if(isTablet){return 'wordcard tablet_wordcard'}
        if(isMobile){return 'wordcard mobile_wordcard'}
    }

    return(
        <Card className={genClassName()}>
            <CardContent>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    {types[word.type]}
                </Typography>
                <Typography variant="h5" component="div">
                    {normalWord(word.word)}
                </Typography>
                <Typography variant="body2">
                    {word.description}
                </Typography>
            </CardContent>
        </Card>
    )
}