import './WordCardTest.css'
import { Card, CardContent, Stack, Typography, Button } from "@mui/material"
import { useStorage } from '../../hook/useStorage.js'

const types = {
    noun:'Существительное',
    adj:'Прилагательное',
    verb:'Глагол',
    partnverbadj:'Причастие/ отглагольное прилагательное',
    details:'Деепричастие',
    adverb:'Наречие'
}

export const WordCardTest = () => {
    let {
        currentTestWord, addGood, addBad, setNewWord
    } = useStorage()

    const enter = (n) => {
        if(n == currentTestWord.good_variable){
            addGood()
        }else{
            addBad()
        }

        setNewWord()
    }

    return (
        <Card>
            <CardContent>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                    {types[currentTestWord.type]}
                </Typography>
                <Typography variant="h5" component="div">
                    {currentTestWord.word.toLocaleLowerCase()}
                </Typography>
                {currentTestWord.interpretation &&
                <Typography variant="caption">
                    ({currentTestWord.interpretation})
                </Typography>
                }
                <div className='buttons_model'>
                    <Stack spacing={2} direction="column">
                        {currentTestWord.variables.map((label, index)=>{
                            return(
                                <Button key = {label} style = {{textTransform: 'none'}} variant="outlined"
                                onClick = {()=>enter(index+1)}
                                >{label}</Button>
                            )
                        })}
                    </Stack>
                </div>
            </CardContent>
        </Card>
    )
}