import { Helmet } from "react-helmet"
import { TestCard } from "../Component/TestCard/TestCard"
import { WordCardTest } from "../Component/WordCardTest/WordCardTest"
import { Card, CardContent, Stack, Typography } from "@mui/material"
import { getRandomWord } from "../helpers/Test"
import { useStorage } from "../hook/useStorage"
import './Test.css'

const CounterCard = ({header, count, ...restProps}) => {
    return(
        <Card {...restProps}>
            <CardContent>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                {header}
                </Typography>
                <Typography variant="h5" component="div">
                {count}
                </Typography>
                {/* <Typography variant="body2">
                    {count}
                </Typography> */}
            </CardContent>
        </Card>
    )
}

export const Test = () => {
    let {
        counter
    } = useStorage()

    return(
        <div>
            <Helmet>
                <title>Тест</title>
            </Helmet>
            <div>
                <div className = 'counter_flex'>
                    <CounterCard className = 'counter_card' header = 'Верные ответы' count = {counter.goods} />
                    <CounterCard className = 'counter_card' header = 'Неверные ответы' count = {counter.bads} />
                </div>
                <WordCardTest />
            </div>
        </div>
    )
}