import { useStorage } from "./hook/useStorage"
import { MenuItem, MenuList, ListItemText, Paper, AppBar, Toolbar, Typography, Box, BottomNavigation, BottomNavigationAction } from "@mui/material"
import QuizOutlinedIcon from '@mui/icons-material/QuizOutlined';
import MenuBookOutlinedIcon from '@mui/icons-material/MenuBookOutlined';

import './menu.css'
import { useLocation, useNavigate } from "react-router-dom"

const labels = [
    ['/test', 'Тест'],
    ['/catalog', 'Словарь']
]

export const Menu = ({children}) => {
    let {
        isMobile, isTablet, isDesktop
    } = useStorage()

    let navigate = useNavigate();
    let location = useLocation()

    console.log(location)

    const defineTabbarValue = () => {
        if(location.pathname.startsWith('/test')){return 0}
        if(location.pathname.startsWith('/catalog')){return 1}
    }

    const onCatalogButtonClick = () => {
        let path_array = location.pathname.split('/').filter(path=>path!='')
        if(path_array.length == 1 && path_array[0] != 'catalog'){
            navigate('/catalog')
        }

        if(path_array.length == 2 && path_array[0] == 'catalog'){
            window.history.back()
        }
    }

    const MenuHeader = () => {
        return(
            <AppBar position="static">
                        <Toolbar>
                            <Typography
                            variant="h6"
                            noWrap
                            component="div"
                            // style = {{width:'100vw'}}
                            // sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                            >
                                {labels.map(label=>{
                                    if(location.pathname.startsWith(label[0])){
                                        return label[1]
                                    }
                                })}
                            </Typography>
                        </Toolbar>
                    </AppBar>
        )
    }

    if(isMobile){
        return(
            <Box>
                <MenuHeader />
                {children}
                <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                    <BottomNavigation
                    showLabels
                    value={defineTabbarValue()}
                    onChange={(event, newValue) => {
                      navigate(labels[newValue][0])
                    }}
                    >
                        <BottomNavigationAction label = 'Тест' icon = {<QuizOutlinedIcon />} />
                        <BottomNavigationAction label = 'Словарь' icon = {<MenuBookOutlinedIcon />} />
                    </BottomNavigation>
                </Paper>
            </Box>
        )
    }

    if(isDesktop || isTablet){
        return(
            <div className='desktop_menu_container'>
                <Paper className={isDesktop ? 'desktop_menu' : 'tablet_menu'} style={{position:'relative'}}>
                <AppBar position="static"><Toolbar></Toolbar></AppBar>
                <div className = 'leftMenu'>
                    <MenuList>
                        <MenuItem onClick = {()=>navigate('/test')} selected = {location.pathname.startsWith('/test')}>
                        <ListItemText>Тест</ListItemText>
                        </MenuItem>

                        <MenuItem onClick = {onCatalogButtonClick} selected = {location.pathname.startsWith('/catalog')}>
                        <ListItemText>Словарь</ListItemText>
                        </MenuItem>
                    </MenuList>
                </div>
                </Paper>
                <Paper className={isDesktop ? 'desktop_content' : 'tablet_content'}>
                    <MenuHeader />
                    {children}
                </Paper>
            </div>
        )
    }
}