import './WordGrid.css'
import { useStorage } from "../../hook/useStorage"

export const WordGrid = ({children}) => {
    let {
        isDesktop,
        isTablet,
        isMobile
    } = useStorage()

    const genClassName = () => {
        if(isDesktop){return 'wordgrid desktop_wordgrid'}
        if(isTablet){return 'wordgrid tablet_wordgrid'}
        if(isMobile){return 'wordgrid mobile_wordgrid'}
    }

    return(
        <div className={genClassName()}>
            {children}
        </div>
    )
}