import { createContext, useState } from 'react'
import useMatchMedia from 'react-use-match-media'
import { getRandomWord } from '../helpers/Test'

export const storageContext = createContext()

const initStorage = () => {
    let goods = localStorage.getItem('goods')
    let bads = localStorage.getItem('bads')
    if(!goods){
        localStorage.setItem('goods', 0)
        goods = 0
    }

    if(!bads){
        localStorage.setItem('bads', 0)
        bads = 0
    }

    return {goods:Number(goods), bads:Number(bads)}
}

export const StorageProvider = ({children}) => {

    const [currentTestWord, setCurrentTestWord] = useState(getRandomWord())
    const isMobile = useMatchMedia('(max-width: 766px)')
    const isTablet = useMatchMedia('(min-width: 767px) and (max-width: 1199px)')
    const isDesktop = useMatchMedia('(min-width: 1200px)')

    const [counter, setCounter] = useState(initStorage())
    
    const clearStorage = () => {
        localStorage.setItem('goods', 0)
        localStorage.setItem('bads', 0)
        setCounter({goods:0, bads:0})
    }
    
    const addGood = () => {
        localStorage.setItem('goods', counter.goods+1)
        setCounter((prev)=>{return {...prev, goods:prev.goods+1}})
    }
    
    const addBad = () => {
        localStorage.setItem('bads', counter.bads+1)
        setCounter((prev)=>{return {...prev, bads:prev.bads+1}})
    }

    const setNewWord = () => { setCurrentTestWord(getRandomWord()) }

    const value = {
        currentTestWord, setCurrentTestWord,
        isMobile, isTablet, isDesktop,
        counter
    }
    
    const funcs = {
        clearStorage, addGood, addBad, setNewWord
    }

    return (
    <storageContext.Provider value={{...value, ...funcs}}>
        {children}
    </storageContext.Provider>
    )

}