import { MenuItem, MenuList, ListItemText } from "@mui/material"
import { Helmet } from "react-helmet"
import { useNavigate } from "react-router-dom"
import { types } from "../data/words"

export const Catalog = () => {
    let navigate = useNavigate()
    
    return(
        <div>
            <Helmet>
                <title>Словарь</title>
            </Helmet>
            <MenuList>
            {types.map((type)=>{
                return(
                    <MenuItem onClick = {
                        ()=>navigate(`/catalog/${type.type}`)
                    }>
                        <ListItemText style = {{textOverflow:'ellipsis'}}>{type.label}</ListItemText>
                    </MenuItem>
                )
            })}
            </MenuList>
        </div>
    )
}